




















import Vue from "vue";
import { mapActions } from "vuex";
export default Vue.extend({
  name: "HomeView",
  data() {
    return {
      items: [
        { title: this.$t("artists"), icon: "person", path: "/artists" },
        { title: this.$t("albums"), icon: "album", path: "/albums" },
        { title: this.$t("tracks"), icon: "audiotrack", path: "/tracks" },
        {
          title: this.$t("playlists"),
          icon: "playlist_play",
          path: "/playlists",
        },
        { title: this.$t("search"), icon: "search", path: "/search" },
      ],
    };
  },
  created() {
    this.setTopBarOptions({
      title: this.$t("musicassistant"),
      transparent: false,
    });
  },
  methods: {
    ...mapActions(["setTopBarOptions"]),
  },
});
